<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query"  clearable @clear="getGoodsList">
            <el-button slot="append" icon="el-icon-search"   @click="getGoodsList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click = "goAddPage"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="goodsList" border style="width: 100%">
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="goods_name" label=""></el-table-column>
        <el-table-column prop="goods_price" label="" width="95px"></el-table-column>
        <el-table-column prop="goods_weight" label=""></el-table-column>
        <el-table-column prop="add_time" label="">
          <template slot-scope="scope">
            {{scope.row.add_time | dateFormat}}
          </template>
        </el-table-column>
        <el-table-column label="" width="300px">
               <template slot-scope="scope">
            <!--  -->
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showEditDialog(scope.row.id)"
            ></el-button>
            <!--  -->
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="removeRoleById(scope.row.goods_id)"
            ></el-button>
              </template>
        </el-table-column>

      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[2, 5,6, 8]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 2
      },
      goodsList: [],
      total: 0
    }
  },
  created() {
    this.getGoodsList()
  },
  methods: {
    async getGoodsList() {
      const { data: res } = await this.$http.get('goods', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      console.log(res.data)
      this.goodsList = res.data.goods
      this.pagenum = res.data.pagenum
      this.total = res.data.total
    },
    handleSizeChange(newSize){
      // pagesize
      this.queryInfo.pagesize = newSize
      this.getGoodsList()
    },
    handleCurrentChange(newPage){
      this.queryInfo.pagenum = newPage
      this.getGoodsList()
    },
    // 
   async removeRoleById(id){
        const confirmResult = await this.$confirm('',
        '',
        {
          confirmButtonText:'',
          cancelButtonText:'',
          type:'warning'
        }
        ).catch(err =>err)
        if(confirmResult !=='confirm'){
          return this.$message.info('')
        }
        // 
        const {data:res} = await this.$http.delete(`goods/${id}`)
        if(res.meta.status !==200){
          return this.$message.error('')
        }
        this.$message.success('')
        this.getGoodsList()
    },
    goAddPage(){
      this.$router.push('/goods/add')
    },
    // 
    showEditDialog(id){
      // 
    }

  }
}

</script>
 <style lang="less" scoped>

</style>