<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getOrderList">
            <el-button slot="append" icon="el-icon-search" @click="getOrderList"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="ordersList" border style="width: 100%">
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="order_number" label=""></el-table-column>
        <el-table-column prop="order_price" label="" width="95px"></el-table-column>
        <el-table-column prop="order_pay" label="" width="120px">
          <template slot-scope="scope">
            <el-tag
              :type="scope.row.order_pay=='0'?'warning':'success'"
            >{{scope.row.order_pay=='0'?'':''}}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label="" width="120px"></el-table-column>
        <el-table-column prop="create_time" label="">
          <template slot-scope="scope">{{scope.row.create_time |dateFormat}}</template>
        </el-table-column>

        <el-table-column label="" width="150px">
          <template slot-scope="scope">
            <!--  -->
            <el-tooltip class="item" effect="dark" content="" :enterable="false">
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="showEditAddressDialog(scope.row.id)"
              ></el-button>
            </el-tooltip>

            <!--  -->
            <el-tooltip class="item" effect="dark" content="" :enterable="false">
              <el-button
                type="success"
                icon="el-icon-location"
                size="mini"
                @click="processById(scope.row.order_id)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[2, 5, 6,8]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addressDialogVisible"
      width="50%"
      @close="addressDialogClosed"
    >
      <el-form
        :model="addressForm"
        :rules="addressFormRules"
        ref="addressFormRef"
        label-width="100px"
      >
        <el-form-item label="" prop="address1">
          <el-cascader :options="cityData" v-model="addressForm.address1"></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="addressForm.address2"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="dialogVisible = false"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="progressVisible" width="50%">
      <!--   -->
      <el-timeline>
        <el-timeline-item
          v-for="(activity, index) in progressInfo"
          :key="index"
          :timestamp="activity.time"
        >{{activity.context}}</el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>
<script>
import cityData from './citydata.js'

export default {
  data() {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 2
      },
      ordersList: [],
      total: 0,
      addressDialogVisible: false,
      addressForm: {
        address1: [],
        address2: ''
      },
      addressFormRules: {
        address1: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ]
      },
      cityData: cityData,
      progressVisible: false,
      progressInfo: {}
    }
  },
  created() {
    this.getOrderList()
  },
  methods: {
    async getOrderList() {
      const { data: res } = await this.$http.get('orders', {
        params: this.queryInfo
      })
      console.log(res)
      if (res.meta.status !== 200)
        return this.$message.error('')
      this.ordersList = res.data.goods
      this.total = res.data.total
      this.queryInfo.pagenum = res.data.pagenum - 0
    },
    handleSizeChange(newSize) {
      this.pagesize = newSize
      this.getOrderList()
    },
    handleCurrentChange(newPage) {
      this.pagenum = newPage
      this.getOrderList()
    },
    // 
    showEditAddressDialog() {
      this.addressDialogVisible = true
    },
    addressDialogClosed() {
      this.addressDialogVisible = false
      this.$refs.addressFormRef.resetFields()
    },
    // 
    async processById(id) {
      this.progressVisible = true
      const { data: res } = await this.$http.get('/kuaidi/' + '1106975712662')
      console.log(res.data, 6644)
      this.progressInfo = res.data
    }
  }
}
</script>
<style lang="less" scoped>
.el-cascader {
  width: 100%;
}
</style>