<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <!--  -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="showAddCateDialog"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <tree-table
        :data="cateList"
        :columns="columns"
        :selection-type="false"
        :expand-type="false"
        show-index
        index-text="#"
        border
        :show-row-hover="false"
      >
        <!--  -->
        <template slot="isOk" slot-scope="scope">
          <i class="el-icon-success" style="color:green;" v-if="scope.row.cat_deleted==false"></i>
          <i class="el-icon-error" style="color:red;" v-else></i>
        </template>
        <!--  -->
        <template slot="order" slot-scope="scope">
          <el-tag size="mini" v-if="scope.row.cat_level==0"></el-tag>
          <el-tag type="success" size="mini" v-else-if="scope.row.cat_level==1"></el-tag>
          <el-tag type="warning" size="mini" v-else></el-tag>
        </template>
        <template slot="opt" slot-scope="scope">
          <el-button
            type="primary"
            icon="el-icon-edit"
            size="mini"
            @click="setEditdialogVisible(scope.row.cat_id)"
          ></el-button>
          <!--  -->
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="mini"
            @click="removeCateById(scope.row.cat_id)"
          ></el-button>
        </template>
      </tree-table>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="showAddDialogVisible"
      width="50%"
      ref="cateRef"
      @close="addCateDialogClose"
    >
      <!--  -->
      <el-form
        :model="addCateForm"
        label-width="100px"
        ref="addCateFormRef"
        :rules="addCateFormRules"
      >
        <el-form-item label="" prop="cat_name">
          <el-input v-model="addCateForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-cascader
            :props="cascaderProps"
            v-model="selectedKeys"
            :options="parentCateList"
            props.expandTrigger="hover"
            @change="parentCateChanged"
            clearable
            change-on-select
          ></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="showAddDialogVisible=false"> </el-button>
        <el-button type="primary" @click="addCate"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="showEditdialogVisible"
      width="50%"
      ref="cateRef"
      @close="editCateDialogClose"
    >
      <!--  -->
      <el-form
        :model="editCateForm"
        label-width="100px"
        ref="editCateFormRef"
        :rules="editCateFormRules"
      >
        <el-form-item label="" prop="cat_name">
          <el-input v-model="editCateForm.cat_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editdialogVisible=false"> </el-button>
        <el-button type="primary" @click="editCate"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryInfo.pagenum"
      :page-sizes="[1, 2, 5, 10]"
      :page-size="queryInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    ></el-pagination>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      cateList: [],
      // 
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 2
      },
      total: 0,
      // 
      columns: [
        {
          label: '',
          prop: 'cat_name'
        },
        {
          label: '',
          type: 'template',
          template: 'isOk'
        },
        {
          label: '',
          type: 'template',
          template: 'order'
        },
        {
          label: '',
          type: 'template',
          template: 'opt'
        }
      ],
      // 
      showEditdialogVisible: false,
      showAddDialogVisible: false,
      // 
      addCateForm: {
        cat_name: '',
        cat_level: 0,
        cat_pid: 0
      },
      // '
      addCateFormRules: {
        cat_name: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ]
      },
      editCateFormRules: {
        cat_name: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ]
      },
      // 
      parentCateList: [],
      // 
      cascaderProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      selectedKeys: [],
      // 
      editCateForm: {
        cat_name: '',
        cat_id: ''
      },
      // 
      addCateFormRules: {
        cat_name: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    // 
    async getCateList() {
      const { data: res } = await this.$http.get('categories/', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200)
        return this.$message.error('')

      this.cateList = res.data.result
      console.log(this.cateList)
      this.total = res.data.total
      // console.log(res.data)
    },
    // 
    showAddCateDialog() {
      this.getParentCateList()
      this.showAddDialogVisible = true
    },
    // 
    addCateDialogClose() {
      this.$refs.addCateFormRef.resetFields()
      this.selectedKeys = []
      this.addCateForm.cat_level = 0
      this.addCateForm.cat_pid = 0
    },
    // 
    addCate() {
      this.$refs.addCateFormRef.validate(async valid => {
        if (!valid) {
          return this.$message.error('')
        }
        const { data: res } = await this.$http.post(
          'categories',
          this.addCateForm
        )
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }

        console.log(res.data, 777777)

        this.$message.success('')
        this.getCateList()
        this.showAddDialogVisible = false
      })
    },
    // 
    async setEditdialogVisible(id) {
      this.showEditdialogVisible = true
      const { data: res } = await this.$http.get('categories/' + id)
      if (res.meta.status !== 200) return this.$message.error('')
      //  console.log(res);
      this.editCateForm = res.data
    },
    // 
    editCate() {
      this.$refs.editCateFormRef.validate(async valid => {
        if (!valid) return this.$message.error('')
        console.log(this.editCateForm.cat_id, 6666)

        const { data: res } = await this.$http.put(
          'categories/' + this.editCateForm.cat_id,
          { cat_name: this.editCateForm.cat_name }
        )
        console.log(res.data)
        this.getCateList()
        this.showEditdialogVisible = false
      })
    },
    // 
    editCateDialogClose() {
      this.$refs.editCateFormRef.resetFields()
      this.showEditdialogVisible = false
    },
    // 
    async removeCateById(id) {
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      console.log(confirmResult)
      // confirm ,cancel
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      // console.log('')
      const { data: res } = await this.$http.delete('categories/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      // 
      // console.log(res)
      this.getCateList()
    },
    // 
    handleSizeChange(newSize) {
      // console.log(newSize);
      this.queryInfo.pagesize = newSize
      this.getCateList()
    },
    // 
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getCateList()
    },
    // 
    async getParentCateList() {
      const { data: res } = await this.$http.get('categories/', {
        paramas: { type: 2 }
      })
      //  console.log(res.data,666666);
      if (res.meta.status !== 200)
        return this.$message.error('')
      this.parentCateList = res.data
    },
    // 
    parentCateChanged() {
      console.log(this.selectedKeys)
      if (this.selectedKeys.length > 0) {
        // id 
        this.addCateForm.cat_pid = this.selectedKeys[
          this.selectedKeys.length - 1
        ]
        this.addCateForm.cat_level = this.selectedKeys.length
        return
      } else {
        this.addCateForm.cat_pid = 0
        this.addCateForm.cat_level = 0
      }
    }
  }
}
</script>

<style lang="less" scoped>
.el-button {
  margin-bottom: 15px;
}
.el-cascader {
  width: 100%;
}
</style>