<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-alert title="" type="warning" show-icon :closable="false"></el-alert>
      <el-row width="500px">
        <el-col>
          <span></span>
          <el-cascader
            width="300px"
            :props="CateProps"
            v-model="selectedCateKeys"
            :options="cateList"
            props.expandTrigger="hover"
            @change="CateChanged"
            clearable
            change-on-select
          ></el-cascader>
        </el-col>
      </el-row>
      <el-tabs v-model="activeName" @tab-click="handleParamsClick">
        <el-tab-pane label="" name="many">
          <el-button
            type="primary"
            :disabled="isBtnDisabled"
            @click="showAddParamsDialogVisible"
          ></el-button>

          <!--  -->
          <el-table :data="manyTableData" style="width: 100%" border stripe>
            <el-table-column type="expand">
               <!--  -->
              <template slot-scope="scope">
                    <el-tag v-for="(itemOne,iOne) in scope.row.attr_vals" :key="iOne" type="success" closable @close=removeRightTagById(iOne,scope.row)>{{itemOne}}</el-tag>
                <!--  -->
                 <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small" 
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                ></el-input>
                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
              </template>
            </el-table-column>
            <el-table-column prop="attr_name" label=""></el-table-column>
            <el-table-column label="">
              <template width="180px" slot-scope="scope">
                <!--  -->
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  size="mini"
                  @click="showeEitParamsDialog(scope.row.attr_id)"
                ></el-button>
                <!--  -->
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="removeCateById(scope.row.attr_id)"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="" name="only">
          <el-button
            type="primary"
            :disabled="isBtnDisabled"
            @click="showAddParamsDialogVisible"
          ></el-button>

          <!--  -->
          <el-table :data="onlyTableData" style="width: 100%" border stripe>
            <el-table-column type="expand">
              <template slot-scope="scope">
                    <el-tag v-for="(item,i) in scope.row.attr_vals" :key="i" type="success" closable @close=removeRightTagById(i,scope.row)>{{item}}</el-tag>
                <!--  -->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small" 
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                ></el-input>
                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
              </template>
            </el-table-column>
            <el-table-column prop="attr_name" label=""></el-table-column>
            <el-table-column label="">
              <template width="180px" slot-scope="scope">
                <!--  -->
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  size="mini"
                  @click="showeEitParamsDialog(scope.row.attr_id)"
                ></el-button>
                <!--  -->
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="removeCateById(scope.row.attr_id)"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>
    <!--  -->
    <el-dialog
      :title="''+titleText"
      :visible.sync="addParamsDialogVisible"
      width="50%"
      @close="addParamsDialogClose"
    >
      <el-form
        :model="addParamsForm"
        :rules="addParamsFormRules"
        ref="addParamsFormRef"
        label-width="100px"
      >
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="addParamsForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addParamsDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addParams"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      :title="''+titleText"
      :visible.sync="editParamsDialogVisible"
      width="50%"
      @close="editParamsDialogClose"
    >
      <el-form
        :model="editParamsForm"
        :rules="editParamsFormRules"
        ref="editParamsFormRef"
        label-width="100px"
      >
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="editParamsForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editParamsDialogVisible"> </el-button>
        <el-button type="primary" @click="editParams"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { async } from 'q'
export default {
  data() {
    return {
      cateList: [],
      CateProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      selectedCateKeys: [],
      activeName: 'many',
      manyTableData: [],
      onlyTableData: [],
      // 
      addParamsDialogVisible: false,
      addParamsForm: {
        attr_name: ''
      },
      addParamsFormRules: {
        attr_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      editParamsForm: {
        attr_name: ''
      },
      editParamsFormRules: {
        attr_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      editParamsDialogVisible: false,
     
      
    }
  },
  created() {
    this.getDataList()
  },
  methods: {
    // 
    async getDataList() {
      const { data: res } = await this.$http.get(`categories`)
      if (res.meta.status !== 200)
        return this.$message.error('')
      // console.log(res.data);
      this.cateList = res.data
    },
    CateChanged() {
      this.getParamsData()
    },
    handleParamsClick() {
      console.log(this.activeName)
      this.getParamsData()
    },
    async getParamsData() {
      if (this.selectedCateKeys.length !== 3) {
        this.selectedCateKeys = []
        this.manyTableData = []
        this.onlyTableData = []
        return
      }
      const { data: res } = await this.$http.get(
        `categories/${this.cateId}/attributes`,
        { params: { sel: this.activeName } }
      )
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      res.data.forEach(item => {
        item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
        // 
        item.inputVisible = false,
        item.inputValue = ''
        console.log(item, 99999)
      })


      if (this.activeName == 'many') {
        this.manyTableData = res.data
      } else {
        this.onlyTableData = res.data
      }
    },

    showAddParamsDialogVisible() {
      this.addParamsDialogVisible = true
    },
    addParamsDialogClose() {
      this.$refs.addParamsFormRef.resetFields()
    },
    addParams() {
      // console.log(this.$refs);
      this.$refs.addParamsFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post(
          `categories/${this.cateId}/attributes`,
          { attr_name: this.addParamsForm.attr_name, attr_sel: this.activeName }
        )
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }
        this.$message.success('')
        // console.log(res);

        this.getParamsData()
        this.addParamsDialogVisible = false
      })
    },
    async showeEitParamsDialog(attr_id) {
      // alert('1111')
      console.log(attr_id)

      this.editParamsDialogVisible = true
      const { data: res } = await this.$http.get(
        `categories/${this.cateId}/attributes/${attr_id}`,
        { params: { sel: this.activeName } }
      )
      // console.log(res.data,666666);

      this.editParamsForm = res.data
    },
    // 
    editParamsDialogClose() {
      this.$refs.editParamsFormRef.resetFields()
    },
    // 
    editParams() {
      this.$refs.editParamsFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.put(
          `categories/${this.cateId}/attributes/${this.editParamsForm.attr_id}`,
          {
            attr_name: this.editParamsForm.attr_name,
            attr_sel: this.activeName
          }
        )
        if (res.meta.status !== 200) return this.$message.error('')
        this.$message.success('')
        this.getParamsData()
        this.editParamsDialogVisible = false
      })
    },
    async removeCateById(attr_id) {
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      console.log(confirmResult,9999966);
      // confirm ,cancel
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      // console.log('')
      const { data: res } = await this.$http.delete(
        `categories/${this.cateId}/attributes/${attr_id}`
      )
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      // 
      this.getParamsData()
    },
    //  
  async  handleInputConfirm(row){
      // console.log(row,11111111);
     if(row.inputValue.trim().length===0){
       row.inputValue = ''
       row.inputVisible = false
       return 
     }
     //  ,
    row.attr_vals.push(row.inputValue.trim())
    row.inputValue = ''
    row.inputVisible = false
   this.saveAttr(row)

    },
    // 
    showInput(row){
     row.inputVisible = true
       this.$nextTick(_ => {
          this.$refs.saveTagInput.$refs.input.focus();
        });
    },
    // 
    removeRightTagById(id,row){
      row.attr_vals.splice(id,1)
       this.saveAttr(row)

    },
  async saveAttr(row){
       
  const {data:res} = await  this.$http.put(`categories/${this.cateId}/attributes/${row.attr_id}`,{attr_name:row.attr_name,attr_sel:row.attr_sel,
    attr_vals:row.attr_vals.join(' ')})
    
    if(res.meta.status!==200) return this.$message.error('')

    this.$message.success('')    
    }

  },
  computed: {
    isBtnDisabled() {
      if (this.selectedCateKeys.length !== 3) {
        return true
      }
      return false
    },
    // ID
    cateId() {
      if (this.selectedCateKeys.length == 3) {
        return this.selectedCateKeys[2]
      }
      return null
    },
    titleText() {
      if (this.activeName === 'many') {
        return ''
      }
      return ''
    }
  }
}
</script>

<style lang="less" scoped>
.el-row {
  margin: 10px 0;
}
.el-input{
  width:300px;
}
.el-tag {
  margin-right:10px
}
</style>