<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible=true"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="roleList" style="width: 100%" border stripe>
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row
              :class="['bdbottom',iOne ===0?'bdtop':'','vcenter']"
              v-for="(itemOne,iOne) in scope.row.children"
              :key="iOne"
            >
              <!--  -->
              <el-col :span="5">
                <el-tag
                  closable
                  @close="removeRightTagById(scope.row,itemOne.id)"
                >{{itemOne.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!--  -->
              <el-col :span="19">
                <el-row
                  v-for="(itemTow,iTow) in itemOne.children"
                  :key="iTow"
                  :class="[iTow ===0?'':'bdtop','vcenter']"
                >
                  <!--  -->
                  <el-col :span="6">
                    <el-tag
                      type="success"
                      closable
                      @close="removeRightTagById(scope.row,itemTow.id)"
                    >{{itemTow.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <!--  -->
                  <el-col :span="18">
                    <el-tag
                      type="warning"
                      v-for="(itemThree,iThree) in itemTow.children"
                      :key="iThree"
                      closable
                      @close="removeRightTagById(scope.row,itemThree.id)"
                    >{{itemThree.authName}}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column prop="roleName" label=""></el-table-column>
        <el-table-column prop="roleDesc" label=" "></el-table-column>
        <el-table-column label="" width="500px">
          <template slot-scope="scope">
            <!--  -->
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showEditDialog(scope.row.id)"
            ></el-button>
            <!--  -->
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="removeRoleById(scope.row.id)"
            ></el-button>
            <!--  -->
            <el-button
              type="warning"
              icon="el-icon-setting"
              size="mini"
              @click="showSetRightDialog(scope.row)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @close="addDialogClose">
      <!--  -->
      <el-form :model="addForm" label-width="140px" ref="addFormRef" :rules="addFormRules">
        <el-form-item label="" prop="roleName">
          <el-input v-model="addForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="addForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addRole"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="50%" @close="editDialogClose">
      <!--  -->
      <el-form :model="editForm" label-width="140px" :rules="editFormRules" ref="editFormRef">
        <el-form-item label="" prop="roleName">
          <el-input v-model="editForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="editForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editUser"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="setRightDialogVisible"
      width="50%"
      @click="setRightDialogClose"
    >
      <!--  -->
      <el-tree
        ref="treeRef"
        :data="rightList"
        :props="treeProps"
        show-checkbox
        node-key="id"
        default-expand-all
        :default-checked-keys="defKeys"
      ></el-tree>

      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRightDialogVisible = false"> </el-button>
        <el-button type="primary" @click="allowRight"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 
      roleList: [],
      // 
      addDialogVisible: false,
      //
      editDialogVisible: false,
      addForm: {
        roleName: '',
        roleDesc: ''
      },
      addFormRules: {
        roleName: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 2,
            max: 10,
            message: '3~10',
            trigger: 'blur'
          }
        ],
        roleDesc: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 6,
            max: 15,
            message: '6~15',
            trigger: 'blur'
          }
        ]
      },

      editForm: {},
      editFormRules: {
        roleName: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 2,
            max: 10,
            message: '3~10',
            trigger: 'blur'
          }
        ],
        roleDesc: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 6,
            max: 15,
            message: '6~15',
            trigger: 'blur'
          }
        ]
      },
      setRightDialogVisible: false,
      // 
      rightList: [],
      treeProps: {
        label: 'authName',
        children: 'children'
      },
      // id
      defKeys: [],
      // id
      roleId: []
    }
  },
  created() {
    this.getRolesList()
  },
  methods: {
    async getRolesList() {
      const { data: res } = await this.$http.get('roles')
      //   console.log(res.data)
      if (res.meta.status !== 200)
        return this.$message.error('')

      this.roleList = res.data
    },
    addDialogClose() {
      this.$refs.addFormRef.resetFields()
    },
    // 
    addRole() {
      this.$refs.addFormRef.validate(async valid => {
        console.log(valid)
        if (!valid) {
          return this.$message.error('!')
        }
        // 
        const { data: res } = await this.$http.post('roles', this.addForm)
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }
        this.$message.success('')
        // 
        this.addDialogVisible = false
        // 
        // console.log(res)
        this.getRolesList()
      })
    },
    //
    async showEditDialog(id) {
      console.log(id)
      const { data: res } = await this.$http.get('roles/' + id)
      // console.log(res,6666);
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.editForm = res.data
      this.editDialogVisible = true
    },
    editDialogClose() {
      this.$refs.editFormRef.resetFields()
    },
    //  
    editUser() {
      this.$refs.editFormRef.validate(async valid => {
        //  console.log(valid)
        if (!valid) {
          return this.$message.error('!')
        }
        // 
        const { data: res } = await this.$http.put(
          'roles/' + this.editForm.roleId,
          { roleName: this.editForm.roleName, roleDesc: this.editForm.roleDesc }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        // 
        this.editDialogVisible = false
        // 
        console.log(res)
        this.getRolesList()
        this.$message.success('')
      })
    },
    // id ,
    async removeRoleById(id) {
      // console.log(id);
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      // console.log(confirmResult);
      // confirm ,cancel
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      // console.log('')
      const { data: res } = await this.$http.delete('roles/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      // 
      console.log(res)
      this.getRolesList()
    },
    // 
    async showSetRightDialog(role) {
      this.roleId = role.id
      // 
      const { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status !== 200)
        return this.$message.error('')
      //  id
      this.getLeafKeys(role, this.defKeys)
      this.rightList = res.data
      // console.log( this.rightList);
      this.setRightDialogVisible = true
    },
    // id defkeys
    getLeafKeys(node, arr) {
      if (!node.children) {
        return arr.push(node.id)
        node.children.forEach(item => this.getLeafKeys(item.arr))
      }
    },
    // 
    setRightDialogClose() {
      this.defKeys = []
    },
    // id
    async removeRightTagById(role, rightId) {
      // 
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      // console.log(confirmResult);
      // confirm ,cancel
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      // console.log('')
      const { data: res } = await this.$http.delete(
        `roles/${role.id}/rights/${rightId}`
      )
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      // 

      // console.log(res)
      role.children = res.data
    },
    // 
    async allowRight() {
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      // console.log(keys);
      const idStr = keys.join(',')
      const { data: res } = await this.$http.post(
        `roles/${this.roleId}/rights`,
        { rids: idStr }
      )
      if (res.meta.status !== 200)
        return this.$message.error('')
      this.$message.success('')
      this.getRolesList()
      this.setRightDialogVisible = false
    }
  }
}
</script>
<style lang="less" scoped>
.el-tag {
  margin: 7px;
}
.bdtop {
  border-top: 1px solid #eee;
}
.bdbottom {
  border-bottom: 1px solid #eee;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>